ib/api/enter'\nimport {router} from '../../../router'\n\nexport default {\n  data () {\n    return {\n      state: api.nowUser.getState(),\n      rankingList: [],\n      timer: null,\n      lastIsWorking: false\n    }\n  },\n  methods: {\n    updateRanking () {\n      dataApi.getBalanceByGame(api.nowUser.getGameId())\n        .then((function (res) {\n          let list = res.data.list\n          let result = []\n\n          list.forEach((function (i, k) {\n            result.push({\n              teamIndex: k + 1,\n              readableTeam: readable.toReadableTeam(k + 1),\n              isTop: false,\n              balance: i,\n              readableBalance: readable.toReadableDollar(i),\n            })\n          }).bind(this))\n\n          result.sort((a, b) => {\n            return a.balance < b.balance\n          })\n\n          let m = result[0].balance\n          for (let i of result) {\n            if (i.balance === m) {\n              i.isTop = true\n            }\n          }\n\n          this.rankingList.splice(0, this.rankingList.length)\n          result.forEach((function (i) {\n            this.rankingList.push(i)\n          }).bind(this))\n        }).bind(this))\n      \n      if (this.state.stage === constant.GAME_STAGE.END) {\n        router.push('/end')\n      }\n    }\n  },\n  computed: {\n    autoPlay () {\n      if (this.lastIsWorking !== this.state.isWorking) {\n        this.lastIsWorking = this.state.isWorking\n        if (this.lastIsWorking) {\n          var audio = document.getElementById('sound-begin')\n          audio.play()\n        } else {\n          var audio = document.getElementById('sound-break')\n          audio.play()\n        }\n      }\n    }\n  },\n  mounted () {\n    this.timer = setInterval(this.updateRanking.bind(this), 1000)\n  }\n}\n</script>\n\n<style>\n.scoreboard .score {\n  padding-top: 30vh;\n  height: 100vh !important;\n}\n\n.scoreboard .clock {\n  padding-top: 30vh;\n  height: 100vh !important;\n}\n\n.scoreboard .game-clock {\n  padding: 0 !important;\n}\n\n.scoreboard .ranking {\n  text-align: center;\n  font-size: 30px;\n  color: rgba(0, 0, 0, 0.6);\n}\n\n.scoreboard .ranking .top-ranking {\n  font-size: 40px;\n  color: rgba(0, 0, 0, 1);\n}\n\n.scoreboard .audio {\n  display: none;\n}\n</style>\n"],"sourceRoot":""}]);

