ed: true,\r\n      errorDialog: false,\r\n      errorDialogMsg: ''\r\n    }\r\n  },\r\n  watch: {\r\n    code () {\r\n      this.btnDisabled = !this.code\r\n    }\r\n  },\r\n  methods: {\r\n    backToRegistNew () {\r\n      router.push('/regist/new')\r\n    },\r\n    setCode () {\r\n      let code = parseInt(this.code)\r\n      if (code < 0 || code > 9999) {\r\n        this.errorDialogMsg = '你的認證碼不正確，應該是由四位數字組成。'\r\n        this.errorDialog = true\r\n      } else {\r\n        this.intoChoose()\r\n      }\r\n    },\r\n    intoChoose () {\r\n      router.push('/choose')\r\n    },\r\n    announce (msg) {\r\n      this.snackbarText = msg\r\n      this.snackbar = true\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style>\r\n.regist .code-text {\r\n  width: 100%;\r\n  margin-top: 10px;\r\n  font-size: 50px;\r\n  line-height: 80px;\r\n  text-align: center;\r\n  background-color: rgba(0, 0, 0, 0.02);\r\n  border: 1px solid #ddd;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

